import Vue from 'vue'
import Router from 'vue-router'
import inicio from './components/inicio'
import principal from './components/principal'
import contenido from './components/contenido'
import exit from './components/exit'
import tipos from './components/tiposdat'
import creditos from './components/creditos'
import introduccion from './components/introduccion'
import gestionescolar from './components/gestionescolar'
import areasgestion from './components/areasgestion'


Vue.use(Router)

export default new Router({
  mode: 'history',
  
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Inicio',
      component: inicio,
    },        
     {
      path: '/principal',
      name: 'principal',
      component: principal,
      children:[
  
      {
          path: '/contenido',
          name: 'contenido',
          component: contenido,
      },
                  
      {
        path: '/exit',
        name: 'exit',
        component: exit,
      },
      {
        path: '/tiposdat',
        name: 'tipos',
        component: tipos,
      },
      {
        path: '/creditos',
        name: 'creditos',
        component: creditos,
      },
      {
        path: '/',
        name: 'introduccion',
        component: introduccion,
      },
      {
        path: '/gestionescolar',
        name: 'Gestionescolar',
        component: gestionescolar
      },
      {
        path: '/areasgestion',
        name: 'Areasgestion',
        component: areasgestion
      }
    ]}
]
})